html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 1;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

nav ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  text-decoration: none;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

/* ここまでリセット用 */

body {
  background-color: black;
  color: white;
}

li {
  list-style: none;
}

.container {
  width: 1170px;
  margin: 0 auto;
  text-align: center;
}

.header {
  height: 60px;
  opacity: 0.8;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 10;
}

.links {
  float: right;
  padding: 5px 10px;
  margin-top: 15px;
}

.top {
  background-image: url(./icon/dartslife-back.png);
  background-size: cover;
  height: 540px;
}

.top-text {
  padding-top: 190px;
}

.top-text h1 {
  font-size: 48px;
  letter-spacing: 5px;
}

.top-text p {
  font-size: 18px;
  padding-top: 25px;
  line-height: 25px;
}

.game-title {
  font-size: 38px;
  padding: 40px 0;
}

.game-select {
  overflow: hidden;
  margin-bottom: 100px;
}

.select {
  float: left;
  width: 33%;
  font-size: 24px;
}

.game-select a {
  color: white;
  display: inline-block;
  width: 150px;
  text-align: right;
  border-radius: 10px;
}

.game-select a:hover {
  opacity: 0.8;
  background-color: #151515;
  transition: 1s;
}

.select p {
  font-size: 14px;
  width: 80%;
  padding-top: 20px;
  text-align: initial;
  margin: 0 auto;
  line-height: 20px;
}

footer {
  background-color: #666;
  height: 40px;
  opacity: 0.7;
}

.footer-icon img {
  height: 30px;
  padding-top: 5px;
  float: left;
}
